"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { generateInterviewDossier } from "@/lib/actions/general.action";

interface InterviewStrategistProps {
  userName: string;
  userId: string;
}

export default function InterviewStrategist({ userName, userId }: InterviewStrategistProps) {
  const [formData, setFormData] = useState({
    jobDescription: "",
    resume: "",
    companyName: "",
    concerns: ""
  });
  const [isGenerating, setIsGenerating] = useState(false);
  const [dossier, setDossier] = useState<string>("");

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleGenerate = async () => {
    if (!formData.jobDescription || !formData.resume || !formData.companyName) {
      alert("Please fill in all required fields (Job Description, Resume, Company Name)");
      return;
    }

    setIsGenerating(true);
    try {
      const result = await generateInterviewDossier({
        jobDescription: formData.jobDescription,
        resume: formData.resume,
        companyName: formData.companyName,
        concerns: formData.concerns,
        userId
      });

      if (result.success && result.dossier) {
        setDossier(result.dossier);
      } else {
        alert("Failed to generate interview dossier. Please try again.");
      }
    } catch (error) {
      console.error("Error generating dossier:", error);
      alert("An error occurred while generating the dossier.");
    } finally {
      setIsGenerating(false);
    }
  };

  const handleCopyToClipboard = () => {
    navigator.clipboard.writeText(dossier);
    alert("Dossier copied to clipboard!");
  };

  return (
    <div className="max-w-4xl mx-auto p-6 space-y-6">
      <div className="text-center space-y-2">
        <h1 className="text-3xl font-bold">Interview Strategist AI</h1>
        <p className="text-gray-600">Generate your personalized Interview Dossier</p>
      </div>

      {!dossier ? (
        <div className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-2">
              <Label htmlFor="companyName">Company Name *</Label>
              <Input
                id="companyName"
                value={formData.companyName}
                onChange={(e) => handleInputChange("companyName", e.target.value)}
                placeholder="e.g., Google, Microsoft, Amazon"
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="concerns">Your Concerns (Optional)</Label>
              <Input
                id="concerns"
                value={formData.concerns}
                onChange={(e) => handleInputChange("concerns", e.target.value)}
                placeholder="e.g., Lack of experience with React, nervous about technical questions"
              />
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="jobDescription">Job Description *</Label>
            <textarea
              id="jobDescription"
              value={formData.jobDescription}
              onChange={(e) => handleInputChange("jobDescription", e.target.value)}
              placeholder="Paste the complete job description here..."
              className="w-full h-40 p-3 border border-gray-300 rounded-md resize-vertical"
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="resume">Your Resume *</Label>
            <textarea
              id="resume"
              value={formData.resume}
              onChange={(e) => handleInputChange("resume", e.target.value)}
              placeholder="Paste your resume content here..."
              className="w-full h-40 p-3 border border-gray-300 rounded-md resize-vertical"
            />
          </div>

          <Button 
            onClick={handleGenerate} 
            disabled={isGenerating}
            className="w-full"
          >
            {isGenerating ? "Generating Dossier..." : "Generate Interview Dossier"}
          </Button>
        </div>
      ) : (
        <div className="space-y-4">
          <div className="flex justify-between items-center">
            <h2 className="text-2xl font-bold">Your Interview Dossier</h2>
            <Button onClick={handleCopyToClipboard} variant="outline">
              Copy to Clipboard
            </Button>
          </div>
          
          <div className="bg-gray-50 p-6 rounded-lg">
            <pre className="whitespace-pre-wrap text-sm font-mono">
              {dossier}
            </pre>
          </div>

          <Button 
            onClick={() => {
              setDossier("");
              setFormData({
                jobDescription: "",
                resume: "",
                companyName: "",
                concerns: ""
              });
            }}
            variant="outline"
            className="w-full"
          >
            Generate New Dossier
          </Button>
        </div>
      )}
    </div>
  );
}
